import React from 'react';
import styles from '../styles/Sidebar.module.css';
import profileImage from '../assets/profile.jpg'; // Import your profile image

// Import icons from react-icons
import { FaEnvelope, FaGithub, FaLinkedin, FaBars, FaTimes } from 'react-icons/fa';

const Sidebar = ({ isOpen, setIsOpen }) => {
  const toggleSidebar = () => {
    setIsOpen(!isOpen);
  };

  // Function to scroll to a specific section smoothly
  const scrollToSection = (id) => {
    const section = document.getElementById(id); // Find the section by its ID
    if (section) {
      section.scrollIntoView({ behavior: 'smooth' }); // Smooth scrolling
    }
    setIsOpen(false); // Close the sidebar after clicking on a link (mobile behavior)
  };

  return (
    <>
      {/* Hamburger Menu for Mobile */}
      <div className={styles.hamburger} onClick={toggleSidebar}>
        {isOpen ? <FaTimes /> : <FaBars />}
      </div>

      {/* Sidebar */}
      <aside className={`${styles.sidebar} ${isOpen ? styles.open : ''}`}>
        {/* Profile Image */}
        <img
          src={profileImage}
          alt="Karim Safiullin"
          className={styles.profileImage}
        />
        <h1>Karim Safiullin</h1>
        <p className={styles.jobTitle}>Senior Data Engineer</p>

        {/* Contact Icons */}
        <div className={styles.contactIcons}>
          <a
            href="mailto:karim.safiullin.career@gmail.com"
            target="_blank"
            rel="noopener noreferrer"
            className={styles.iconLink}
          >
            <FaEnvelope />
          </a>
          <a
            href="https://github.com/BondaiKa"
            target="_blank"
            rel="noopener noreferrer"
            className={styles.iconLink}
          >
            <FaGithub />
          </a>
          <a
            href="https://linkedin.com/in/karim-safiullin"
            target="_blank"
            rel="noopener noreferrer"
            className={styles.iconLink}
          >
            <FaLinkedin />
          </a>
        </div>

        {/* Navigation Links */}
        <nav>
          <ul className={styles.navLinks}>
            <li onClick={() => scrollToSection('about')}>About</li>
            <li onClick={() => scrollToSection('skills')}>Skills</li>
            <li onClick={() => scrollToSection('experience')}>Experience</li>
            <li onClick={() => scrollToSection('education')}>Education</li>
            <li onClick={() => scrollToSection('projects')}>Projects</li>
          </ul>
        </nav>

        {/* Footer */}
        <footer className={styles.footer}>
          <p>¬© 2024 Karim Safiullin</p>
        </footer>
      </aside>
    </>
  );
};

export default Sidebar;
